import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { PublicMethods } from '../../../core/_public/-public-methods'
import { HttpClient } from '@angular/common/http'
let _url = PublicMethods.createURL
@Injectable({
  providedIn: 'root'
})

export class MembersService {
  url

  private membershipRenew=new BehaviorSubject(null);
  private _saveReschdualPackage=new BehaviorSubject(null);
  private Url=new BehaviorSubject("");
  private member = new BehaviorSubject("");
  private membership = new BehaviorSubject(null);
  private submittedMembership = new BehaviorSubject(false)
  private selectedMembership = new BehaviorSubject(null)
  private dataOfSelectedPackage=new BehaviorSubject(null)
  private MembershipType=new BehaviorSubject('');
  _savedReschdualPackage=this._saveReschdualPackage.asObservable();
  _membershipRenew=this.membershipRenew.asObservable();
  submittedValue = this.submittedMembership.asObservable()
  currentmember = this.member.asObservable()
  currentMembershipType=this.MembershipType.asObservable()
  currentDataofSelectedPackage= this.dataOfSelectedPackage.asObservable()
  currentMembership = this.membership.asObservable();
  currentSelectedMembership = this.selectedMembership.asObservable()
  currentUrl=this.Url.asObservable();

  constructor(private http: HttpClient) { }
  saveReschdualPackage(saveReschdualPackage)
  {
   this._saveReschdualPackage.next(saveReschdualPackage);
  }
  changeuser(member) {
    this.member.next(member)
  }
  changemembership(membership) {
    this.membership.next(membership)
  }
  changeSubmittedMembership(value) {
    this.submittedMembership.next(value)
  }
  DataOfPackage(membership)
  {
    this.dataOfSelectedPackage.next(membership)
  }
  changeSelectedMembership(selectedMembership) {
    this.selectedMembership.next(selectedMembership)
  }
  changeType(type)
  {
    this.MembershipType.next(type);
  }
  changeUrl(url)
  {
    this.Url.next(url)
  }
  renewmembership(status)
  {
    this.membershipRenew.next(status)
  }
  getAllMembers(pages, prepage, search,status='',type='',gender='',payment='',orderby='',order=-1) {
    var API_getAllUsers_URL = _url('customers', 'getallmembers', '', '', '', pages, prepage, search);
    if(status!='') API_getAllUsers_URL += '&status='+status;
    if(type!='') API_getAllUsers_URL += '&type='+type;
    if(gender!='') API_getAllUsers_URL += '&gender='+gender;
    if(payment!='') API_getAllUsers_URL += '&payment='+payment;
    if(orderby!='') API_getAllUsers_URL += '&orderby='+orderby+'&order='+order;
    return this.http.get(API_getAllUsers_URL);
  }
  getMembership(pub_key, pages, prepage, search,status,schedual,payment,ids,orderby,order) {
    var API_getAllUsers_URL = _url('membership', 'getSelectedpackage', pub_key, '', '', pages, prepage, search)
    if(status!='') API_getAllUsers_URL += '&status='+status;
    if(schedual!='') API_getAllUsers_URL += '&schedual='+schedual;
    if(payment!='') API_getAllUsers_URL += '&payment='+payment;
    if(ids!='') API_getAllUsers_URL += '&ids='+ids;
    if(orderby!='') API_getAllUsers_URL += '&orderby='+orderby+'&order='+order;
    return this.http.get(API_getAllUsers_URL);
  }
  updateMembership(id, editData) {
    return this.http.patch(_url('membership', 'updateSelectedpackage', id), editData)
  }
  selectedPackages(pub_key,type,downgrade,upgrade,membership_id,units,branch) {
    var url = _url('membership', 'getPackages', '', '', '', null, null, null, pub_key,null,type,membership_id,downgrade,upgrade);
    if(units) url=url+'&units_key='+units;
    if(branch) url=url+'&branch_key='+branch;
    return this.http.get(url)
  }
  addPackage(packag) {
    return this.http.post(_url('membership', 'selectePackage', ''), packag)
  }

  upgradePackage(packag) {
    return this.http.post(_url('membership', 'upgradePackage', ''), packag)
  }
  downgradePackage(packag) {
    return this.http.post(_url('membership', 'downgradePackage', ''), packag)
  }

  getAllActivity() {
    return this.http.get(_url('activities', 'getActivity', ''))
  }
  getPackageBYId(id) {
    return this.http.get(_url('membership', 'getPackagesById', id))
  }
  getSelectedPackageById(id) {
    return this.http.get(_url('membership', 'getSelectedPackageById',id))
  }

  getActivityById(id) {
    return this.http.get(_url('activities', 'getActivityById',id))
  }

  updateSpacificMemberMemebership(membership_id,updatedData)
  {
    return this.http.patch(_url('membership','paymentPackage',membership_id),updatedData)
  }
  //bind active class of wizard
  ActivePage()
  {
    this.currentUrl.subscribe(res=>{
      this.url=res
      console.log(this.url)
      return this.url

    })
  }
 isSelectedPackage(callback)
  {
   this.currentSelectedMembership.subscribe(res=>{
    if(res==null){
     callback(true);
    } else {
      callback(false);
    }
    })
  }
  viewAttendance(data) {
    const API_getschedual_URL = _url('attendance', 'viewAttendance', '', '', '', 1, 10000000,'')
    return this.http.post<any>(API_getschedual_URL,data);
  }
  reschedualAttendance(data)
  {
    return this.http.post(_url('attendance','reschedualAttendance','','',''),data)
  }

 renewMebership(pub_key,data)
 {
   return this.http.post(_url('membership','renewmembership',pub_key,'',''),data)
 }
 getMemberById(id)
 {
   return this.http.get(_url('customers','getmember',id))
 }
 cancelAttendance(id)
 {
 return this.http.post(_url('attendance','cancelAttendance',''),id)
 }

 upload_file() {
  return _url('media', 'uploadimage', '', '', '');
 }
}
